
import './App.css';

import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import React, { useRef, useEffect, useState } from 'react';

import { Wrapper } from './module/Wrapper';

function App() {
  const [cards, setCards] = useState([]);
  const id = useRef(0);
  const addCard = ()=>{
    id.current+=1
    let obj = {
      name:''+id.current,
      id:id.current,
      displayDef:''+id.current,
      operator: '',
    }
    

    setCards([...cards,obj])
  }
  return (
    <div className="App">
      <div><button onClick={addCard}></button></div>

      <div >
        <DndProvider backend={HTML5Backend}>
            <Wrapper
                cards={cards}
                setCards={setCards}
                isReadOnly={false}
            />
        </DndProvider>
      </div>

    </div>
  );
}

export default App;
